import React, {Component} from "react";
import LabeledInput from "eshop/modules/core/components/hoc/LabeledInput";
import {getCommonPropsForInput} from "eshop/modules/checkout/components/representative/common"
import {
    getCountryName,
    getCountrySuggestions,
    getInputValidationParams,
    getPropsForIdentification,
    getPropsForInput,
    getPropsForSelect,
    mapCountrySuggestion,
    prepareForeignerIdentificationTypes
} from "../../utils/utils";
import SalesChannel from "../../../core/enum/SalesChannel";
import {OraFloatingLabelSelect} from "../../../../components/OraFloatingLabelSelect";
import DatePickerByDocumentTypeComponent from "../datepicker/DatePickerByDocumentTypeComponent";

export default class Representative extends Component {

    constructor(props) {
        super(props);
    }

    remove(event) {
        event.preventDefault();
        this.props.remove(this.props.index);
    }

    renderHeader() {
        return this.props.label && (
            <div className="u-spacing-top-xl">
                <div className="o-separator u-spacing-m"></div>
                <div className="u-position-relative">
                    <p className="h5">{this.props.label}</p>
                    {this.props.remove && <a href="#" className="u-position-absolute u-position-bottom u-position-right" onClick={this.remove.bind(this)}>Usuń</a>}
                </div>
            </div>
        );
    }

    toggleForeigner() {
        let foreignerEnabled = !this.props.foreigner;
        this.props.setRepresentativeDataNoValidations({name: 'foreigner', value: foreignerEnabled, index: this.props.index});
        if (foreignerEnabled) {
            let defaultValue = prepareForeignerIdentificationTypes(this.props.foreignerIdentificationMap).find(t=>"RESIDENCE_CARD" === t.value);
            this.props.setRepresentativeDataNoValidations({name: 'identification', value: defaultValue.value, index: this.props.index});
        }
    }

    render() {
        return (
            <div className="u-spacing-top-l">
                {this.renderHeader()}
                <div className="l-row">
                    <div className="u-no-margin-top u-spacing-top-xs u-spacing-l l-col l-col-12" hidden={this.props.foreignerB2bDisabled || (this.props.channels.sales !== SalesChannel.POS)}>
                        <label className="o-checkbox opl-checkbox">
                            <input type="checkbox"
                            onChange={this.toggleForeigner.bind(this)}
                            disabled={(this.props.channels.sales !== SalesChannel.POS)}
                            checked={this.props.foreigner}/>
                            <span className="o-ci"></span>
                            <span className="o-ci-label">{this.props.descriptions.foreigner}</span>
                        </label>
                    </div>
                    <div className="l-col l-col-12">
                        <LabeledInput {...getCommonPropsForInput(this.props, "firstName", "representative", {label:"Imię"})} virtualKeyboard={true}/>
                    </div>
                    <div className="l-col l-col-12 u-spacing-top-l">
                        <LabeledInput {...getCommonPropsForInput(this.props, "lastName", "representative", {label:"Nazwisko"})} virtualKeyboard={true}/>
                    </div>
                    <div className="l-col l-col-6 u-spacing-top-l">
                        <LabeledInput {...getCommonPropsForInput(this.props, "pesel", "representative", {label:"PESEL", mask: "99999999999", placeholder: "___________"})} />
                    </div>
                    <div hidden={this.props.foreigner} className="l-col l-col-6 u-spacing-top-l">
                        <LabeledInput {...getCommonPropsForInput(this.props, "idNumber", "representative", {label:"Numer dowodu", mask: "aaa999999", placeholder: "_________"})} />
                    </div>
                    <div hidden={!this.props.foreigner} className="l-col l-col-6 u-spacing-top-l">
                        <LabeledInput
                            {...getPropsForInput('country', this.props, this.props.setRepresentativeData, this.props.setRepresentativeDataNoValidations)}
                            {...getInputValidationParams('country', this.props)}
                            value={getCountryName(this.props.foreignCountries,this.props.country)}
                            suggestions={getCountrySuggestions(this.props.foreignCountries,this.props.country)}
                            mapSuggestion={mapCountrySuggestion}
                            onBlur={this.props.changeRepresentativeCountry}
                            autoComplete={true}
                            minLength={1}
                            index={this.props.index}
                        />
                    </div>
                    <div hidden={!this.props.foreigner}>
                        <div className="l-col l-col-12 u-spacing-top-l">
                            <OraFloatingLabelSelect
                                {...getPropsForSelect('identification', this.props, this.props.setRepresentativeData)}
                                {...getInputValidationParams('identification', this.props)}
                                className={"g-gray1-bg u-font-small"}
                                options={prepareForeignerIdentificationTypes(this.props.foreignerIdentificationMap)}
                                isFeedbackParam="true"
                                withEmptyOption={false}
                                index={this.props.index}
                            />
                        </div>
                    </div>
                    <div hidden={!this.props.foreigner}>
                        <div className="l-col l-col-6 u-spacing-top-l">
                            <LabeledInput
                                {...getPropsForIdentification('identificationValue', true, this.props, this.props.setRepresentativeData, this.props.setRepresentativeDataNoValidations)}
                                {...getInputValidationParams('identificationValue', this.props)}
                            />
                        </div>
                        <div className="l-col l-col-6 u-spacing-top-l">
                            <DatePickerByDocumentTypeComponent
                                {...this.props}
                                onChange={this.props.setRepresentativeData}
                                startDateName="identificationRegisterDate"
                                endDateName="identificationEndDate"
                                index={this.props.index}
                                identificationRegistrationDate={this.props.identificationRegisterDate}
                                identificationExpirationDate={this.props.identificationEndDate}
                            />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}